.tera {
  display: flex;
  width: 90vw;
  margin: 4vw auto;
  margin-bottom: 0;
  align-items: flex-start;
}
.tera > img {
  margin-top: 8.5vw;
  width: 19.2vw;
  margin-left: 4.5vw;
}
.inp {
  width: 57.5vw;
}
.flexis {
  display: flex;
  align-items: center;
  margin-bottom: 2vw;
  justify-content: space-between;
}
.att {
  display: flex;
  align-items: flex-start;
  margin-top: 4vw;
}
.att span {
  width: 80%;
  color: #a8b0b7;
  font-family: Montserrat;
  font-size: 0.8vw;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -0.8px;
}
.att img {
  width: 2.3vw;
}
.att b {
  color: black;
}
.flexis h2 {
  text-align: center;
  font-family: Montserrat;
  font-size: 5vw;
  font-style: normal;
  font-weight: 600;
  line-height: 120%; /* 120px */
  letter-spacing: -4px;
  margin-right: 1.8vw;
}
.flexis input {
  border-radius: 0.5vw;
  background: #f5f7f9;
  padding: 0.8vw 1.5vw;
  color: var(--, #a8b0b7);
  font-family: Inter;
  font-size: 1.3vw;
  font-style: normal;
  font-weight: 600;
  line-height: 100%; /* 20px */
  letter-spacing: -1px;
  border: none;
  outline: none;
  width: 26vw;
}
#kolom {
  width: 57.5vw;
}
.numsic {
  width: 57.5vw;
  display: flex;
  justify-content: space-between;
}
.linik {
  width: 2px;
  background: #a8b0b7;
  height: 0.5vw;
  margin-bottom: 0.3vw;
}
.numsic > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1vw;
}
input[type="range"] {
  height: 1.9270833333333335vw;
  -webkit-appearance: none;
  margin: 0.5208333333333333vw 0;
  width: 100%;
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 1.9270833333333335vw;
  cursor: pointer;
  animate: 0.2s;

  background: #71d578;
  border-radius: 0.26041666666666663vw;
  border: 0.20833333333333334vw solid #f2f3f5;
}
input[type="range"]::-webkit-slider-thumb {
  border: 0.052083333333333336vw solid #ffffff;
  height: 3.125vw;
  width: 3.125vw;
  border-radius: 0.552083333333333336vw;
  background: url(./img/th.svg);
  background-size: cover;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -0.8333333333333334vw;
}
input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #71d578;
}
input[type="range"]::-moz-range-track {
  width: 100%;
  height: 1.9270833333333335vw;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0.052083333333333336vw 0.052083333333333336vw
    0.052083333333333336vw #353535;
  background: #71d578;
  border-radius: 0.26041666666666663vw;
  border: 0.20833333333333334vw solid #f2f3f5;
}
input[type="range"]::-moz-range-thumb {
  border: 0.052083333333333336vw solid #ffffff;
  height: 3.125vw;
  width: 3.125vw;
  border-radius: 0.052083333333333336vw;
  background: #fff6f6;
  cursor: pointer;
}
input[type="range"]::-ms-track {
  width: 100%;
  height: 1.9270833333333335vw;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type="range"]::-ms-fill-lower {
  background: #71d578;
  border: 0.20833333333333334vw solid #f2f3f5;
  border-radius: 0.052083333333333336vw;
}
input[type="range"]::-ms-fill-upper {
  background: #71d578;
  border: 0.20833333333333334vw solid #f2f3f5;
  border-radius: 0.052083333333333336vw;
  box-shadow: 0.052083333333333336vw 0.052083333333333336vw
    0.052083333333333336vw #353535;
}
input[type="range"]::-ms-thumb {
  margin-top: 0.052083333333333336vw;
  box-shadow: 0.052083333333333336vw 0.052083333333333336vw
    0.052083333333333336vw #353535;
  border: 0.052083333333333336vw solid #ffffff;
  height: 3.125vw;
  width: 3.125vw;
  border-radius: 0.052083333333333336vw;
  background: #fff6f6;
  cursor: pointer;
}
input[type="range"]:focus::-ms-fill-lower {
  background: #71d578;
}
input[type="range"]:focus::-ms-fill-upper {
  background: #71d578;
}
