.work {
  width: 90vw;
  margin: 4vw auto;
  margin-top: -4vw;
}
.workBody {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.rightWork {
  position: relative;
  margin-right: -5vw;
}
.rightWork img {
  position: relative;

  width: 64vw;
}
.leftWork h2 {
  font-family: Montserrat;
  font-size: 5vw;
  font-style: normal;
  margin-top: 14vw;
  font-weight: 600;
  line-height: 120%; /* 120px */
  letter-spacing: -5px;
  margin-bottom: 3.5vw;
}
.leftWork p {
  color: #0370f3;
  font-family: Montserrat;
  font-size: 1.8vw;
  font-style: normal;
  font-weight: 500;
  line-height: 150%; /* 54px */
  letter-spacing: -1.8px;
}
.leftWork h3 {
  color: #000;
  font-family: Montserrat;
  font-size: 2.5vw;
  font-style: normal;
  font-weight: 600;
  line-height: 120%; /* 57.6px */
  letter-spacing: -2.4px;
  margin-bottom: 4.5vw;
}
.leftWork h3 span {
  color: #000;
  font-family: Montserrat;
  font-size: 5vw;
  font-style: normal;
  font-weight: 600;
  line-height: 120%; /* 115.2px */
  letter-spacing: -4.8px;
}
.leftWork div {
  color: #626262;
  font-family: Montserrat;
  font-size: 1.8vw;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 54px */
  letter-spacing: -1.8px;
}
.fleetMine {
  border-radius: 1.5vw;
  background: #f5f7f9;
  padding: 3.8vw 2.7vw;
  position: relative;
  margin-top: 8vw;
}
.fleetMine img {
  position: absolute;
  width: 21.5vw;
  right: 3.8vw;
  bottom: 0;
}
.fleetMine p {
  color: #000;
  font-family: Montserrat;
  font-size: 2.5vw;
  font-style: normal;
  margin-bottom: 1vw;
  font-weight: 600;
  line-height: 120%; /* 57.6px */
  letter-spacing: -2.4px;
}
.fleetMine span {
  color: #626262;
  font-family: Montserrat;
  font-size: 1.8vw;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -1.8px;
}

@media (max-width: 640px) {
  .workBody {
    flex-direction: column;
  }
  .leftWork h2 {
    font-family: Montserrat;
    font-size: 12vw;
    font-style: normal;
    margin-top: 14vw;
    font-weight: 600;
    line-height: 120%;
    letter-spacing: -2px;
    margin-bottom: 3.5vw;
  }
  .leftWork p {
    color: #0370f3;
    font-family: Montserrat;
    font-size: 5vw;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    letter-spacing: -0.8px;
  }
  .leftWork h3 {
    color: #000;
    font-family: Montserrat;
    font-size: 6.5vw;
    font-style: normal;
    font-weight: 600;
    line-height: 120%;
    letter-spacing: -1.4px;
    margin-bottom: 10.5vw;
  }
  .leftWork h3 span {
    color: #000;
    font-family: Montserrat;
    font-size: 12vw;
    font-style: normal;
    font-weight: 600;
    line-height: 120%;
    letter-spacing: -0.8px;
  }
  .leftWork div {
    color: #626262;
    font-family: Montserrat;
    font-size: 5vw;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: -0.8px;
    margin-bottom: 9vw;
  }
  .fleetMine {
    border-radius: 5.5vw;
    background: #f5f7f9;
    padding: 4.8vw 5.7vw;
    position: relative;
    margin-top: 8vw;
    padding-bottom: 0;
  }
  .sliderr {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90vw;
    margin: 0 auto;
    margin-top: 7vw;
  }

  .sliderBody {
    display: flex;
    width: 100%;
    justify-content: space-between;
    overflow-x: scroll;
  }
  .sliderBody img {
    width: 59vw;
  }
  .fleetMine span {
    color: #626262;
    font-family: Montserrat;
    font-size: 5.5vw;
    font-style: normal;
    font-weight: 500;
    margin-top: 5vw;
    display: block;
    line-height: normal;
    letter-spacing: 0.2px;
  }
  .fleetMine p {
    color: #000;
    font-family: Montserrat;
    font-size: 8.5vw;
    font-style: normal;
    margin-bottom: 1vw;
    font-weight: 600;
    line-height: 120%;
    letter-spacing: -0.4px;
  }
  .fleetMine img {
    position: static;
    width: 100%;
    margin-top: 5vw;
    bottom: 0;
  }
  .rightWork {
    position: relative;
    margin-right: 0;
  }
  .rightWork {
    position: relative;
    margin-right: 0;
    left: -5vw;
  }
  .rightWork img {
    position: relative;
    width: 100vw;
  }
}
