.FAQ {
  width: 90vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}
.FAQ h2 {
  color: var(--, #000);
  font-family: Montserrat;
  font-size: 5vw;
  margin-top: 2.5vw;
  margin-bottom: 2.5vw;
  font-style: normal;
  font-weight: 600;
  line-height: 110%; /* 110px */
  letter-spacing: -4px;
}
.faqEmBody {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.faqEm {
  border: 1px solid rgba(245, 247, 249, 1);
  border-radius: 1vw;
  padding: 2vw;
  margin-bottom: 1.5vw;
  cursor: pointer;
}
.faqEmBody p {
  color: #000;
  font-family: Montserrat;
  font-size: 1.5vw;
  font-style: normal;
  font-weight: 600;
  line-height: 150%; /* 42px */
  letter-spacing: -1.4px;
}
.faqEmBody img {
  width: 2.5vw;
}
.faqEm > p {
  margin-top: 2vw;
  color: var(--, #000);
  font-family: Montserrat;
  font-size: 1.2vw;
  width: 90%;
  overflow: hidden;
  transition: 0.5s;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 36px */
  letter-spacing: -1.2px;
}
@media (max-width: 640px) {
  .FAQ {
    width: 90vw;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 auto;
  }
  .faqEm {
    border: 1px solid rgba(245, 247, 249, 1);
    border-radius: 5vw;
    padding: 4vw;
    width: 100%;
    margin-bottom: 3.5vw;
    cursor: pointer;
  }
  .faqEmBody img {
    width: 7.5vw;
  }
  .faqEm > p {
    margin-top: 2vw;
    color: var(--, #000);
    font-family: Montserrat;
    font-size: 4vw;
    width: 90%;
    overflow: hidden;
    transition: 0.5s;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: -0.2px;
  }
  .faqEmBody p {
    color: #000;
    font-family: Montserrat;
    font-size: 4vw;
    font-style: normal;
    font-weight: 600;
    width: 80%;
    line-height: 150%;
    letter-spacing: -0.4px;
  }
  .FAQ h2 {
    color: var(--, #000);
    font-family: Montserrat;
    font-size: 11vw;
    margin-top: 9.5vw;
    margin-bottom: 7.5vw;
    font-style: normal;
    font-weight: 600;
    line-height: 110%;
    letter-spacing: -1px;
  }
}
